import React from "react";
import NavBar from "./NavBar";
import News from "./News";
import { Routes, Route } from "react-router-dom";
import SearchResults from "./SearchResults";
import Footer from "./Footer";
import About from "./About";
import Contact from "./Contact"
import LoadingBar from "react-top-loading-bar";
import { useState } from 'react'

export function Home() {
  const [progress, setProgress] = useState(0)
  return (
    <>
    <LoadingBar
        color='#f11946'
        progress={progress}
        onLoaderFinished={() => setProgress(0)}
      />
      <NavBar curr="home" />

      <Routes>
        <Route path="/search/*" element={<SearchResults searchQuery="*" />} />
        <Route
          exact
          path="/bussiness/*"
          element={<News category="bussiness" />}
        />
        <Route
          exact
          path="/entertainment/*"
          element={<News category="entertainment" />}
        />
        <Route exact path="/general/*" element={<News category="general" />} />
        <Route exact path="/*" element={<News category="general" />} />
        <Route exact path="/health/*" element={<News category="health" />} />

        setProgress(30);
        <Route exact path="/about" element={<About setProgress={setProgress} />} />
        setProgress(100);
        <Route exact path="/contact" element={<Contact setProgress={setProgress} />} />
        <Route
          exact
          path="/technology/*"
          element={<News category="technology" />}
        />
        <Route exact path="/sports/*" element={<News category="sports" />} />
      </Routes>
      <div className="footer">
        <Footer />
      </div>
    </>
  );
}
export default Home;
