import React, { useEffect } from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import SearchBar from "./SearchBar";
import Sidebar from "./Sidebar";
import { useLocation } from "react-router-dom";

export function NavBar( props){
  let curr=props.curr;
    const location = useLocation();
    if(location.pathname==="/about"){
      curr="about";
    }
    else if(location.pathname==="/contact"){
      curr="contact";
    }


    useEffect(()=>{document.getElementById(curr).classList.add("active");},[curr]);
    return(
      <>
        <Navbar bg="black" data-bs-theme="dark" expand="lg" fixed="top" className="bg-body-tertiary" >
      <Container>
        <Navbar.Brand href="/home">
            <div className="logo"></div>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link id="home" href="/home">Home</Nav.Link>
            <Nav.Link id="contact" href="/contact">Contact</Nav.Link>
            <Nav.Link id="about" href="/about">About</Nav.Link>
          </Nav>
          <SearchBar/>
          <div className="sideCanvas">
          < Sidebar/>
          </div>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    </>
)
}
export default NavBar;