import React from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { Badge } from 'react-bootstrap';


function NewsCard(props) {
  return (
    
    <Card id="card" >
      <Badge id="badge" bg="danger">{props.source}</Badge>
      <Card.Img variant="top" height={"200px"} src={props.imageURL!=null?props.imageURL:"https://www.macedonrangeshalls.com.au/wp-content/uploads/2017/10/image-not-found.png"} />
      <Card.Body>
        <Card.Title>{props.title!=null?props.title:"Title Not Found"}</Card.Title>
        <Card.Text>
          {props.content!=null?props.content:props.description!=null?props.description:"Description Not Found"}
        </Card.Text>
        <p className='smallHeadings'><span>Author-</span> {props.author!=null?props.author:"unknown"}</p>
        <p className='smallHeadings'><span>Published at-</span> {props.publishedAt!=null?new Date(props.publishedAt).toLocaleString('en-GB', { timeZone: 'IST' }):"unknown"}</p>
        <a target='_blank' rel='noreferrer' href={props.newsURL}>
          <Button id="read-more" variant="dark">Read More</Button>
        </a>
      </Card.Body>
    </Card>
  );
}


export default NewsCard;
