import { useState } from "react";
import Button from "react-bootstrap/Button";
import Offcanvas from "react-bootstrap/Offcanvas";
import { Link } from "react-router-dom";

function Sidebar() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button id="cat-btn" variant="light" onClick={handleShow}>
        Category{" "}
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="16"
          height="9"
          viewBox="0 0 16 9"
        >
          <path
            fill="currentColor"
            d="M7.18 7.62L3.03 2.79c-.6-.7-.1-1.79.82-1.79h8.29c.93 0 1.42 1.09.82 1.79L8.82 7.62c-.43.5-1.21.5-1.64 0Z"
          />
        </svg>
      </Button>

      <Offcanvas show={show} onHide={handleClose} >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Categories</Offcanvas.Title>
        </Offcanvas.Header>
        <hr />
        <Offcanvas.Body>
        
          <Link className="cats" to="/bussiness">Business</Link>
          <hr/>
          <Link className="cats" to="/entertainment">Entertainment</Link>
          <hr/>
          <Link className="cats" to="/general">General</Link>
          <hr/>
          <Link className="cats" to="/health">Health</Link>
          <hr/>
          <Link className="cats" to="/science">Science</Link>
          <hr/>
          <Link className="cats" to="/technology">Technology</Link>
          <hr/>
          <Link className="cats" to="/sports">Sports</Link>
          <>
        </>
        </Offcanvas.Body>
      </Offcanvas>
    </>
  );
}

export default Sidebar;
