import React from "react";
import { useEffect } from "react";

 function About({setProgress}) {
  useEffect(() => {
    setProgress(100);
  }, [setProgress]);
    document.title="About Us";
  return (
        
    <div className="about">
      <h2 className="about-heading">About Us</h2>
      <p className="about-text">
        Welcome to our platform! This is my Project, and we're thrilled to have you here. Our mission is to provide you with the latest news and updates from various categories, ensuring you stay informed about what's happening around the world.
      </p>
      <p className="about-text">
        Our dedicated team of journalists and editors work tirelessly to curate the most relevant and engaging news articles. We cover a wide range of topics, including business, entertainment, technology, health, science, and more.
      </p>
      <p className="about-text">
        We believe that access to accurate and up-to-date information is essential in today's fast-paced world. Whether you're interested in global headlines or specific niche topics, you'll find it all here.
      </p>
      <p className="about-text">
        Thank you for choosing us as your source for news. We're committed to delivering high-quality content that informs, educates, and entertains. Explore our website and stay connected with the world through our news coverage.
      </p>
    </div>
  );
}

export default About;
