import React, { useEffect, useState } from "react";
import NewsCard from "./NewsCard";
import LoadingBar from "react-top-loading-bar";
import InfiniteScroll from "react-infinite-scroll-component";
import { Spinner } from "react-bootstrap";

export function News(props) {
  const [progress, setProgress] = useState(0);
  const [articles, setArticles] = useState([]);
  const [totalResults, settotalResults] = useState(0);
  const [page, setPage] = useState(1);
  useEffect(() => {
    const url = `https://newsapi.org/v2/top-headlines?country=in&category=${props.category}&apiKey=c9782f8ef371488abc0f8821dae48578`;
    setArticles([]);
    const fetchArticles = async () => {
      setProgress(0);
      try {
        setProgress(30);
        const response = await fetch(url);

        if (!response.ok) {
          throw new Error(
            `Failed to fetch: ${response.status} - ${response.statusText}`
          );
        }
        const data = await response.json();
        setArticles(data.articles);
        settotalResults(data.totalResults);
      } catch (error) {
        console.log(error);
      }
      setProgress(100);
      document.title = `NewsEcho - Top ${props.category} headlines`;
    };

    fetchArticles();
  }, [props.category]);

  const fetchMoreData = async () => {
    setPage(page + 1);
    let url = `https://newsapi.org/v2/top-headlines?country=in&category=${props.category}&apiKey=c9782f8ef371488abc0f8821dae48578&page=${
      page + 1
    }`;

    setProgress(0);
    try {
      setProgress(30);
      const response = await fetch(url);
      const data = await response.json();
      setArticles(articles.concat(data.articles));

    } catch (error) {
      console.log(error);
    }

    setProgress(100);
  };

  return (
    <>
      <div className="top-heading">
        <h4>&nbsp; NewsEcho - Top {props.category} headlines</h4>
      </div>
      <div className="news-holder">
        <LoadingBar
          color="red"
          progress={progress}
          onLoaderFinished={() => setProgress(0)}
        />
        {
          <>
            <InfiniteScroll
              dataLength={articles.length}
              next={fetchMoreData}
              hasMore={articles.length !== totalResults}
              loader={
                <div className="center">
                  <Spinner animation="grow" />Loading...
                </div>
              }
              scrollableTarget="scrollableDiv"
            >
              {articles.length === 0? <div className="center"><Spinner animation="grow" />Loading...</div>: null}
              <div className="news-holder">
                {" "}
                {articles.map((article, index) => (
                  <NewsCard
                    description={
                      article.description && article.description.length > 200
                        ? article.description.slice(0, 200) + "..."
                        : article.description
                    }
                    key={index}
                    title={
                      article.title && article.title.length > 80
                        ? article.title.slice(0, 80) + "..."
                        : article.title
                    }
                    content={
                      article.content && article.content.length > 200
                        ? article.content.slice(0, 200) + "..."
                        : article.content
                    }
                    imageURL={article.urlToImage}
                    newsURL={article.url}
                    author={article.author}
                    publishedAt={article.publishedAt}
                    source={article.source.name}
                  />
                ))}
              </div>
            </InfiniteScroll>
          </>
        }
      </div>
    </>
  );
}

export default News;
